.row{
    margin-top: 25px;
    margin-left: 20px;
    color: #fff;
}
.row h2{
    padding-left: 20px;
}
.posters{
    cursor: pointer;
    display: flex;
    padding: 20px;
    overflow-x: auto;
    overflow-y: hidden;
    scroll-behavior: smooth;
}
.poster:hover{
    transform: scale(1.1);
}
.action_poster:hover{
    transform: scale(1.1);
}
.posters::-webkit-scrollbar{
    display: none;
}

.poster{
    max-height: 200px;
    padding-right: 15px;  
}
.action_poster{
    max-height: 150px;
    padding-right: 15px;  
} 
