.banner{
    height: 550px;
    background-size: cover;
    color: #fff;
   
}
.content{
    padding-top: 180px;
    height: 190px;
    padding-left: 15px;
}
.title{
    font-size: 2rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
    padding-left: 50px;
}
.buttens{
    padding-left: 50px;
}
.butten{
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-right: 1.5rem;
    padding-left: 1.5rem;
    padding-top: 0.5px;
    padding-bottom: 0.5px;
    background-color: rgba(51, 51, 51, 0.5);
    cursor: pointer;
    margin-right: 1rem;
    
}
button:hover{
    color: #111;
    background-color: #e6e6e6;
}
.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 360px;
    padding-left: 50px;
}
.fade_bottem{
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111); 
    margin-top: 62px;
    position: absolute;
}