.Row{
    padding: 20px;
    margin-bottom: 50px;
    color: #fff;
    height: 380px;
}
.Row h2{
    padding-left: 20px;
    padding-bottom: 20px;
}
.SectionImage{
    height: 330px;
    width: 50%;
    margin-left: 20px;
    border-radius: 50px;
    background-size: cover;
    float: left;
}
.SectionText{  
    color: #fff;
    width: 40%; 
    float: left;
    padding-left: 65px;
    height: 340px;
    overflow-y: auto;
    scroll-behavior: smooth;
}
.SectionText p{
    text-align: justify;
    margin-right: 20px;
}
.SectionText h1{
    margin-top: 100px;
    text-align: center;
    font-size: 45px;
    font-weight: 800;
}
.SectionText h2{
    margin-top: 30px;
    text-align: justify;
}
.SectionText p span {
    color: blue;
}
.SectionImg_promo{
    height: 250px;
    width: 25%;
    padding-left: 30px;
    margin-right: 150px;
    margin-top: 60px;
    border-radius: 50px;
    background-size: cover;
    float: right;
}